<template>
	<div class="personal-content-body">
		<div class="content">
			<div class="content-title clear">
				<div class="left">招股书</div>
				<el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getArticleDetails(1)">
					<i slot="prefix" class="el-input__icon el-icon-search  "></i>
				</el-input>
			</div>
			<!-- <div class="search">
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time" :clearable='true' @change="getArticleDetails(1)">
          <i slot="prefix" class="el-input__icon el-icon-search  "></i>
        </el-input>
      </div> -->
			<div class="list-contaienr">
				<div class="list-header">
					<span>{{totalTxt}}</span>

					<!-- <div class="file_down_btn ">
                          <span class="iconfont icon-daoru"></span>
                          <span>文件下载</span>
                      </div> -->
				</div>
				<div class="list-line"></div>
				<!-- <div class="list-nav">
          <span @click="goBack('tuneUpAssistantList')">尽调助手&nbsp;&nbsp;></span>
          <span>招股书</span>
        </div> -->

				<div class="list-content-items">
					<div class="item_title" v-if="total!=0">
						<div class="item_title_name">
							文件标题
						</div>
						<div class="item_title_icon">

						</div>
						<div class="item_title_size">文件大小</div>
						<div class="item_title_time">时间</div>
					</div>
					<div class="list-content-item" v-for="(item,index) of dataList" :key="index">
						<img src="../../images/icon_pdf.png" class="item_file_type" alt="">
						<div class="item_file_name">
							<div class="item_file_name_info" @click="toShowFilePage(item.pdf_name,item.url)"> {{item.pdf_name}}</div>
						</div>
						<a :href="item.url"> <span class="iconfont icon-daoru"></span></a>
						<span class="item_file_size">{{item.size==null||item.size==''?'未知':item.size}}</span>
						<span class="item_file_time">{{item.date==null||item.date==''?'未知':item.date}}</span>
					</div>
					<div class="table-nodata flex_center" v-if="total==0">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span>
					</div>
					<div class="pagination-normal" v-if="total!=0">
						<el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
						</el-pagination>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		industryReport
	} from "../../config/api";
	import {
		dialogConfirm,
		dialogConfirmHasbtn
	} from "../../config/mUtils";
	export default {
		data() {
			return {
				currentPage: 1,
				keyWord: "",
				pageSize: 10,
				total: '',
				totalTxt: '',
				dataList: []
			};
		},
		mounted() {
			this.params = this.$route.query;
			this.getArticleDetails(1);
		},
		methods: {
			//获取详情信息
			currentChange(val) {
				if(this.total > this.pageSize) {
					this.getArticleDetails(val);
				}
			},
			getArticleDetails(val) {
				industryReport({
					type: 1,
					page: val,
					pageSize: this.pageSize,
					keywords: this.keyWord
				}).then(res => {
					if(res.data.msgNo == 1) {
						if(res.data.payload != null && res.data.payload != '') {
							this.total = res.data.payload.count
							this.totalTxt = '共' + res.data.payload.count + '条结果';

							this.dataList = res.data.payload.data;
						} else {
							this.total = 0
							this.totalTxt = '共0条结果';
							this.dataList = [];
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			},
			goBack(name) {
				this.$router.push({
					name: name
				});
			},
			toShowFilePage(file_name, path2) {
				let url = path2;
				const {
					href
				} = this.$router.resolve({
					name: "documentView",
					query: {
						filetype: "pdf",
						url: url
					}
				});
				window.open(href, "_blank");
			},
			todownLoad(href, name) {
				let a = document.createElement("a");
				a.href = href;
				a.download = name;
				a.click();
			}
		}
	};
</script>

<style lang="scss" scoped>
	.content {
		width: 100%;
		min-height: 100%;
		background-color: white;
		padding: 20px 30px;
		// padding-top: 30px;
	}
	
	.hidden {
		display: none;
	}
	
	.content-title>div.left {
		font-size: 18px;
		line-height: 30px;
		color: #35383a;
		font-weight: bold;
	}
	
	.header-right-time {
		margin-right: 10px; // position: relative;
	}
	
	.list-contaienr {
		// border: 1px solid #e4e4e4;
		// padding: 0px 17px;
		position: relative;
	}
	
	.search {
		margin-bottom: 10px;
	}
	
	.list-header {
		margin-bottom: 13px;
		display: flex;
		align-items: center;
		position: relative;
		&>span {
			text-align: left;
			color: #3c98e8;
			font-size: 14px;
			font-weight: bold;
			margin-right: 37px;
		}
	}
	
	.list-nav {
		// padding-left: 14px;
		margin-bottom: 12px;
	}
	
	.list-nav span {
		font-size: 14px;
		color: #3c98e8;
		cursor: pointer;
	}
	
	.list-nav span:last-child {
		color: #35383a;
		cursor: inherit;
	}
	
	.list-line {
		border-bottom: 1px solid #f4f4f4;
		margin-bottom: 15px;
	}
	
	.file_down_btn {
		display: flex;
		align-items: center;
		width: 108px;
		height: 34px;
		border: 1px solid #3c98e8;
		border-radius: 6px;
		justify-content: center;
		position: absolute;
		right: 34px;
		cursor: pointer;
		&>span {
			color: #3c98e8;
		}
		&>span:nth-child(1) {
			font-size: 25px;
			margin-right: 5px;
		}
		&>span:nth-child(2) {
			font-size: 14px;
		}
	}
	
	.item_title {
		display: flex;
		height: 60px;
		line-height: 60px;
		padding-left: 50px;
		justify-content: flex-end;
		font-size: 14px;
		margin-top: 15px;
		// margin-bottom: 6px;
		background: #F3F6FB;
		.item_title_name {
			width: calc(73% - 50px);
			color: #808080;
		}
		.item_title_icon {
			width: 51px;
			color: #808080;
		}
		.item_title_size {
			width: 12%;
			color: #808080;
		}
		.item_title_time {
			width: 15%;
			color: #808080;
		}
	}
	
	.list-content-item {
		display: flex;
		align-items: center;
		// padding-bottom: 30px;
		padding: 20px 0px;
		border-top: 1px solid #f1f1f1;
		.item_file_type {
			width: 30px;
			height: 30px;
			margin-right: 20px;
		}
		.item_file_name {
			display: flex;
			flex-direction: column;
			width: calc(75% - 110px);
			padding-right: 60px;
			// position: relative;
			// top: 15px;
			.item_file_name_info {
				color: #35383a;
				font-size: 16px;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				// margin-bottom: 10px;
				cursor: pointer;
			}
			.item_file_name_info:hover {
				color: #3c98e8;
			}
			.item_file_name_type {
				// width: auto;
				font-size: 14px;
				padding: 2px 5px;
				color: #3c98e8;
				background-color: #e7f4ff;
			}
		}
		.iconfont {
			font-size: 20px;
			margin-right: 30px;
			color: #3c98e8;
			cursor: pointer;
		}
		.item_file_size,
		.item_file_time {
			font-size: 14px;
			color: #35383a;
		}
		.item_file_size {
			width: 10%;
		}
		.item_file_time {
			width: 15%;
		}
	}
	
	// .list-content-item:nth-child(odd) {
	//   background-color: #f6fbff;
	// }
	.list-content-item:hover {
		background-color: #F6FBFF
	}
	
	// .list-content-item:last-child{
	//   border-bottom: 1px solid #f1f1f1;
	// }
	.table-nodata{
		height: 300px;
	}
</style>


